




























































import { Component, Vue } from 'vue-property-decorator'

@Component<AppLayout>({
  created () {
    this.$accessor.updateSettings()
  }
})
export default class AppLayout extends Vue {
  get navItems () {
    return [
      {
        name: 'Random',
        to: '/random',
        icon: 'random'
      },
      {
        name: 'Quiz',
        to: '/quiz',
        icon: 'chalkboard-teacher'
      },
      {
        name: 'Hanzi',
        to: '/hanzi',
        character: '字'
      },
      {
        name: 'Vocab',
        to: '/vocab',
        character: '词'
      },
      {
        name: 'Sentence',
        to: '/sentence',
        character: '句'
      },
      {
        name: 'Extra',
        to: '/extra',
        icon: 'user-edit'
      },
      {
        name: 'Library',
        to: '/library',
        icon: 'book-reader'
      },
      {
        name: 'Level',
        to: '/level',
        text: this.level
      },
      {
        name: 'Settings',
        to: '/settings',
        icon: 'cog'
      },
      {
        name: 'About',
        href: 'https://github.com/zhquiz/zhquiz-desktop',
        icon: ['fab', 'github']
      }
    ]
  }

  openInNewTab = window.parent?.openExternal || ((url: string) => window.open(url, '_blank', 'noopener noreferrer'))

  get level () {
    const { level } = this.$store.state.settings
    return level ? level.toString() : ' '
  }
}
